@import '../../styles/variables.scss';
@import '../../styles/mixin.scss';

.chat-header {
    width: 100%;
    background-color: $bg-elements;
    border-bottom: $border;
}

.chat-header__text {
    font-size: 30px;
    color: $color-text;
    font-weight: 600;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
}

.chat-header__wrap {
    display: flex;
    align-items: center;
    padding: 5px 8px;
    margin-left: 60px;
    gap: 15px;
    @include laptop() {
        width: 90%;
        margin: 0 auto;
        justify-content: space-between;
        gap: 0px;
    }
    @include flexible(600px){
        width: 98%;
    }
}

.chat-header__img {
    width: 55px;
    height: 55px;
    border-radius: 50%;

}

.chat-header__arrow {
    margin-right: 0px;
    fill: $color-text;
    transition: .2s;
    width: 45px;
    cursor: pointer;
    height: 45px;
    &>svg {
        width: inherit;
        height: inherit;
    }

    &:hover {
        fill: $color-orange-hover;
    }

    @include tablet() {
        margin: 0px;
    }
}